module.exports = "/* GLOBAL STYLES\n-------------------------------------------------- */\n/* Padding below the footer and lighter body text */\nbody {\n    padding-top: 3rem;\n    padding-bottom: 3rem;\n    /*color: #5a5a5a; */\n  }\n/* CUSTOMIZE THE CAROUSEL\n  -------------------------------------------------- */\n/* Carousel base class */\n.carousel {\n    margin-bottom: 4rem;\n  }\n/* Since positioning the image, we need to help out the caption */\n.carousel-caption {\n    bottom: 3rem;\n    z-index: 10;\n  }\n/* Declare heights because of positioning of img element */\n.carousel-item {\n    height: 16rem;\n  }\n.carousel-item > img {\n    position: absolute;\n    top: 0;\n    left: 0;\n    min-width: 100%;\n    height: 16rem;\n  }\n/* MARKETING CONTENT\n  -------------------------------------------------- */\n/* Center align the text within the three columns below the carousel */\n.marketing .col-lg-4 {\n    margin-bottom: 1.5rem;\n    text-align: center;\n  }\n.marketing h2 {\n    font-weight: 400;\n  }\n/* rtl:begin:ignore */\n.marketing .col-lg-4 p {\n    margin-right: .75rem;\n    margin-left: .75rem;\n  }\n/* rtl:end:ignore */\n/* Featurettes\n  ------------------------- */\n.featurette-divider {\n    margin: 5rem 0; /* Space out the Bootstrap <hr> more */\n  }\n/* Thin out the marketing headings */\n.featurette-heading {\n    font-weight: 300;\n    line-height: 1;\n    /* rtl:remove */\n    letter-spacing: -.05rem;\n  }\n/* RESPONSIVE CSS\n  -------------------------------------------------- */\n@media (min-width: 40em) {\n    /* Bump up size of carousel content */\n    .carousel-caption p {\n      margin-bottom: 1.25rem;\n      font-size: 1.25rem;\n      line-height: 1.4;\n      color: black;\n    }\n  \n    .carousel-caption h1 {\n        color: black;\n    }\n    \n    \n   /*The location of this matters, when carousel-control etc. were lower in the css, the arrows wouldnt\n   show up when the website was in its mobile version*/ \n   \n    .carousel-control-next,\n  .carousel-control-prev /*, .carousel-indicators */ {\n      -webkit-filter: invert(100%);\n              filter: invert(100%);\n  }\n  \n    .featurette-heading {\n      font-size: 50px;\n    }\n  }\n@media (min-width: 62em) {\n    .featurette-heading {\n      margin-top: 7rem;\n    }\n    \n  }\nh1 {\n    font-size: 70px;\n  }\ndiv[name=\"head\"] {\n    background-image: linear-gradient(to right,teal, pink);\n    color: white;\n  }\n\n\n/*# sourceMappingURL=data:application/json;base64, */"